.project {
    border: 1px solid;
    width: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.ProjectImgContainer {
    height: 200px;
    border: 1px solid;
    background-color: #d4bf38;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}

.projectBottom {
    width: 100px;
    height: 30px;
    border: 1px solid;
    background-color: #e5ddd2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.descriptionBlock {
    width: 100%;
    background-color: #fdfcfc;
    border: 1px solid;
    text-align: start;
    padding: 10px;
    box-sizing: border-box;
}

.titleProject {
    background-color: #e5e5e5;


}

.textProject {
    background-color: #e5e5e5;

}