.skillsBlock {
    padding: 50px 0;
    background-color: #98fa98;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    box-sizing: border-box;
}

.title {
    background-color: ghostwhite;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;
}
