.contactsBlock {
    padding: 50px 0;
    background-color: #fcdd88;
}

.contactsContainer {
    flex-direction: column;
    justify-content: space-between;
}

.contactsTitle {
    background-color: #e5ddd2;
}

.formBox {
    border: 1px solid;
    width: 250px;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
}

input {
    height: 18%;
}

textarea {
    height: 50%;
}

button {
    width: 100px;
    background-color: #e5ddd2;
    margin: 10px;
}