.MyProjectsBlock {
    padding: 50px 0;
    background-color: #e5ddd2;
}

.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    box-sizing: border-box;
}

.title {
    background-color: ghostwhite;
}

.projects {
    width: 100%;
    border: 1px solid;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;

}