.footerBlock {
    padding: 50px 0;
    background-color: #ffcad6;
}
.footerContainer{
    flex-direction: column;
}
.footerTitle{
    background-color: #fdfcfc;
    margin: 10px;
}
.boxInFooterContainer{
    border: 1px solid;
    width: 250px;
    height: 60px;
    display: flex;
    justify-content: space-between;

}
.icon{
    width: 60px;
    height: 100%;
    background-color: #a19e9d;
}


.footerBottomText{
    background-color: #fdfcfc;
}